import { PropsWithChildren } from "react"
import { UseQueryResult } from "@tanstack/react-query"
import { WatchlistDataTableEmptyCell } from "./WatchlistDataTableEmptyCell"
import { WatchlistDataTableErrorCell } from "./WatchlistDataTableErrorCell"
import { WatchlistDataTableLoadingCell } from "./WatchlistDataTableLoadingCell"

type PriceCellProps = PropsWithChildren<{
  value?: unknown
  queryResult?: Pick<UseQueryResult<unknown, Error>, "isLoading" | "error">
}>

function WatchlistDataTableDataStatusCell({ queryResult, value, children }: PriceCellProps) {
  return (
    <WatchlistDataTableErrorCell error={queryResult?.error}>
      <WatchlistDataTableLoadingCell isLoading={queryResult?.isLoading}>
        <WatchlistDataTableEmptyCell value={value}>{children}</WatchlistDataTableEmptyCell>
      </WatchlistDataTableLoadingCell>
    </WatchlistDataTableErrorCell>
  )
}

export { WatchlistDataTableDataStatusCell }
